<template>
	<div class="body">
		<div class="search_group">
			<div class="search_group_top">
				<div class="search_box">
					<el-input v-model="simple_name" prefix-icon="el-icon-search" placeholder="基金简称" clearable>
					</el-input>
				</div>
			</div>
			<div class="search_group_bottom flex_start">
				<div class="search_box">
					<el-input v-model="fund_name" placeholder="合伙企业" clearable>
					</el-input>
				</div>
				<div class="search_box">
					<el-select v-model="is_master" clearable placeholder="基金类型" >
						<el-option v-for="item in fund_type" :key="item.value" :label="item.label" :value="item.value">
						</el-option>
					</el-select>
				</div>
				<div class="date_box">
					<el-date-picker v-model="start_protocol_time" type="date"  :editable='false' placeholder="选择开始日期" value-format="yyyy-MM-dd" :clearable="false">
    					</el-date-picker>
				</div>
				<div class="date_middle_text">
					至
				</div>
				<div class="date_box">
					<el-date-picker v-model="end_protocol_time" type="date"   :editable='false' placeholder="选择截止日期" value-format="yyyy-MM-dd" :clearable="false">
    					</el-date-picker>
				</div>
				<div class="btn_box">
					<button @click="getSelfFundList(1)">搜索</button>
				</div>
			</div>
		</div>
		
		<div class="line_box">
			<div class="line"></div>
		</div>
		
		<div class="main under_fund">
			<div class="under_fund_header flex_between">
				<div class="">
					自管基金总数<span>(&nbsp;{{ total }}&nbsp;)</span>
				</div>
				<div class="flex_end">
					<!-- <button class="hover_btn" @click="toFundRunListPage">
						运行监测报表总览						
					</button> -->
					<button class="hover_btn flex_center" @click="downloadSelfFundListExcel">
						<span class="iconfont icon-daoru"></span>
						导出excel表格
					</button>
					<button class="hover_btn flex_center" @click="downloadSelfFundInfoExcel">
						<span class="iconfont icon-daoru"></span>
						下载自管基金信息模板
					</button>
					
					<div class="">
						<label for="upload-fund-input">
							<div class="table-group-title-btn btn-blue-border">
								<span class="iconfont icon-daochu"></span>导入自管基金信息
							</div>
						</label>
						<input type="file" ref="inputer" id="upload-fund-input" style="display:none" v-on:change="uploadSelfFundInfoExcel($event)" />
					</div>
					
					<button class="under_fund_header_btn" @click="addMotherFund">
						<span class="iconfont icon-jia"></span>
						添加自管基金
					</button>
				</div>
			</div>
			<div class="under_fund_main table">
				<el-table ref="multipleTable" :data="tableData" border style="width: 100%">
					<el-table-column prop="index" label="序号" width="80">
					</el-table-column>
					<el-table-column sortable prop="simpleName" label="基金简称" width="250">
					</el-table-column>
					<el-table-column sortable prop="id" label="ID" width="80">
					</el-table-column>
					
					<el-table-column sortable prop="fundName" label="合伙企业" width="250">
					</el-table-column>
					<el-table-column sortable prop="is_master" label="基金类型" width="120">
					</el-table-column>
					<el-table-column sortable prop="unit" label="币种" width="100">
					</el-table-column>
					<el-table-column sortable prop="subscription_amount" label="认缴规模（万元）" width="180">
					</el-table-column>
					<el-table-column sortable prop="amount_paid_in" label="实缴规模（万元）" width="180">
					</el-table-column>
					<el-table-column sortable prop="paid_in_proportion" label="缴款比例" width="150">
						<template slot-scope="scope" >
							{{ scope.row.paid_in_proportion }}&nbsp;{{scope.row.paid_in_proportion!=0?'%':''}}
						</template>
					</el-table-column>
					<el-table-column sortable prop="multiple" label="倍数" width="100">
					</el-table-column>
					<el-table-column sortable prop="check_multiple" label="复核倍数" width="130">
					</el-table-column>
					<el-table-column sortable prop="irr" label="XIRR" width="100">
					</el-table-column>
					<el-table-column sortable prop="external_payment" label="对外认缴人民币（万元）" width="200">
					</el-table-column>
					<el-table-column sortable prop="external_payment_meiyuan" label="对外认缴美元（万元）" width="200">
					</el-table-column>
					<el-table-column sortable prop="external_payment_fact" label="对外实缴人民币（万元）" width="200">
					</el-table-column>
					<el-table-column sortable prop="external_payment_fact_meiyuan" label="对外实缴美元（万元）" width="200">
					</el-table-column>
					<el-table-column sortable prop="protocol_end_date" label="协议到期日" width="120">
					</el-table-column>
					<!-- <el-table-column sortable prop="end_date" label="基金到期日" width="120">
					</el-table-column> -->
					
					<el-table-column sortable prop="duration" label="基金期限（年）" width="150">
					</el-table-column>
					<el-table-column sortable prop="record_state_name" label="备案状态" width="120">
					</el-table-column>
					<el-table-column sortable prop="is_change" label="工商状态" width="120">
					</el-table-column>
					<el-table-column sortable prop="check_date" label="核准日期" width="120">
					</el-table-column>
					<el-table-column sortable prop="sg_company_manager_name" label="管理公司" width="250">
					</el-table-column>
					
					<el-table-column fixed="right" label="操作" width="280">
						<template slot-scope="scope">
							<el-button @click="setUserPower(scope.row)" type="text" size="small">设置权限</el-button>
							<!--<el-button @click="toFundFilePage(scope.row)" type="text" size="small">基金报告</el-button>-->
							<el-button @click="toFundRunPage(scope.row)" type="text" size="small">基金运行</el-button>
							<el-button @click="view(scope.row)" type="text" size="small">查看</el-button>
							<el-button @click="deleteFund(scope.$index,scope.row)" type="text" size="small">删除</el-button>
						</template>
					</el-table-column>
					<template slot="empty">
						<div class="table-nodata">
							<img src="../../images/table_nodata.png"> 没有对应数据
						</div>
					</template>
				</el-table>
				<el-pagination @size-change="everyPageSizeChange" prev-text="上一页" next-text="下一页" background @current-change="changeCurrentPage" :current-page="currentPage" :page-size="pageSize" layout="sizes, prev, pager, next, jumper" :total="total">
    				</el-pagination>
			</div>
		</div>
		<!--  设置权限弹窗开始-->
		<el-dialog title="设置可见人员" :visible.sync="isSetPowerDialogShow" width="813px" center :append-to-body='true'>
			<set-power v-bind:have-power-user-ids="havePowerUserIds" v-on:get-set-power-id="getSetPowerIds"></set-power>
			<div slot="footer" class="dialog_footer clear">
				<button class="dialog_btn dialog_btn_cancel" @click="isSetPowerDialogShow=false">取消</button>
				<button class="dialog_btn dialog_btn_submit" @click="setPowerSubmit">确定</button>
			</div>
		</el-dialog>
		<!--设置权限弹窗结束-->
	</div>
</template>

<script>
	import setPower from '../../components/setPower.vue'
	import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils'
	import { getSelfFundListApi,downloadSelfFundInfoExcelApi,uploadSelfFundInfoExcelApi,getHavePowerUsersSelfFundApi,setPowerSubmitSelfFundApi,deleteSelfFundApi,downloadSelfFundListExcelApi } from '../../config/api'
	
	export default {
		name: 'selfAdministerFundList',
		data() {
			return {
				company_id: '',
				currentPage: 1,
				pageSize: 10,
				total: 0,
				fund_name: '',
				simple_name: '',
				is_master: '',
				start_protocol_time: '',
				end_protocol_time: '',
				
				isSetPowerDialogShow: false,
				havePowerUserIds: [],
				subComponentPostIds: [],
				ids: '', //设置权限时选择的用户ids
				noPowerTest: '您没有查看此内容的权限，请联系管理员。',
				tableData: [],
				fund_type: [
					{
						label: '分支自管基金',
						value: 0
					},
					{
						label: '主体自管基金',
						value: 1
					}
				],
			}
		},
		created() {
			this.getSelfFundList()
		},
		mounted() {},
		components: {
			setPower
		},
		methods: {
			getSelfFundList(is_init_page){
				if (is_init_page) {
					this.currentPage = 1;
				}
				if(this.start_protocol_time&&this.end_protocol_time){
					let data1 = new Date(this.start_protocol_time);
					let data2 = new Date(this.end_protocol_time);
					if(data1.getTime() > data2.getTime()) {
						dialogConfirm("info", "开始时间不能大于结束时间");
						return false;
					}
				}
				
				getSelfFundListApi({
					page: this.currentPage,
					size: this.pageSize,
					fund_name: this.fund_name,
					simple_name: this.simple_name,
					is_master: this.is_master,
					start_protocol_time: this.start_protocol_time,
					end_protocol_time: this.end_protocol_time,
				}).then(res => {
					if(res.data.msgNo == 1) {
						let data = res.data.payload.data
						data.forEach((element,index) => {
							element.index = (this.currentPage-1)*this.pageSize+index+1;
							if(element.is_change==1){
								element.is_change = '待变更'
							}else if(element.is_change==0){
								element.is_change = '正常'
							}
							if(element.is_master==1){
								element.is_master = '主体自管基金'
							}else if(element.is_master==0){
								element.is_master = '分支自管基金'
							}
							if(element.unit==1){
								element.unit = '人民币'
							}else if(element.unit==2){
								element.unit = '美元'
							}
							
							element.paid_in_proportion =parseInt(element.paid_in_proportion*100)/100;
						
						});
						this.total = res.data.payload.count;
						this.tableData = data;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			addMotherFund(){
				this.$router.push({
					name: "selfAdministerFundAdd"
				})
			},
			toFundRunPage(row){
				this.$router.push({
					name: "selfAdministerFundRun",
					query: {
						id: row.id,
						type: 2
					}
				})
			},
			//基金报告
			toFundFilePage(row){
				if (row.auth.fund_operate) {
					this.$router.push({
						name: "investorReport",
						query: {
							fundId: row.id,
							page:this.page,
							type:2,
						}
					})
				} else{
					dialogConfirm("info", '您无权限操作该条数据');
				}
				
			},
			deleteFund(index,row){
				// console.log(row);
				var id = row.id;
				if (row.auth.auth_delete) {
					dialogConfirmHasbtn('确定要删除该基金吗？').then(() => {
						this.toDeleteCompany(id)
					}).catch(() => {});
				} else{
					dialogConfirm("info", '您无权限操作该条数据');
				}
			},
			// 判断删除的时候是否是最后一条记录
			isLeftOneItem(){
				let length = this.tableData.length;
				if(length == 1 && this.currentPage > 1){
					this.currentPage = this.currentPage - 1;
				}
			},
			toDeleteCompany(id){
				deleteSelfFundApi({
					id: id
				}).then(res => {
					if(res.data.msgNo == 1) {
						dialogConfirm("success", res.data.message);
						
						this.isLeftOneItem()
						
						this.getSelfFundList()
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			view(e){
				// console.log(e);
				if (e.auth.check_operate) {
					this.$router.push({
						name: 'selfAdministerFundCompanyDetail',
						query: { id: e.id }
					})
				} else{
					dialogConfirm("info", noPowerTest);
				}
			},
			// 设置权限功能模块
			setUserPower(row){
				this.company_id = row.id;
				if(row.auth.auth_operate) {
					this.getHavePowerUsers(row.id);
				} else {
					dialogConfirm("info", "您没有设置权限，请联系管理员！");
				}
			},
			getHavePowerUsers(id) {
				getHavePowerUsersSelfFundApi({
					type: 5,
					data_id: id
				}).then(res => {
					// console.log(res.data);
					if(res.data.msgNo == 1) {
						this.havePowerUserIds = res.data.payload.ids;
						this.ids = res.data.payload.ids.join(',');
						this.isSetPowerDialogShow = true;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			// 获取设置权限组件返回来的ids
			getSetPowerIds(idNodes) {
				// console.log(idNodes);
				this.subComponentPostIds = [];
				for(let i = 0, max = idNodes.length; i < max; i++) {
					if(this.subComponentPostIds.indexOf(idNodes[i]) == -1) {
						this.subComponentPostIds.push(idNodes[i])
					}
				}
				var postIds = this.decodeSelectedUserId(this.subComponentPostIds);
				this.ids = postIds.join(',');
			},
			setPowerSubmit() {
				setPowerSubmitSelfFundApi({
					table_name: 'pet_fund',
					type: 5,
					data_id: this.company_id,
					ids: this.ids
				}).then(res => {
					// console.log(res.data);
					if(res.data.msgNo == 1) {
						this.isSetPowerDialogShow = false;
						dialogConfirm("success", res.data.message);
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			//给加标识的员工id进行解码还原为id数组
			decodeSelectedUserId(data) {
				var user_ids = '';
				var selectedEmployeeId = [];
				var splitArr = [];
				for(var i = 0, max = data.length; i < max; i++) {
					splitArr = data[i].id.split('-');
					if(selectedEmployeeId.indexOf(splitArr[0]) == -1) {
						selectedEmployeeId.push(splitArr[0]);
					}
				}
				return selectedEmployeeId
			},
			
			// 下载基金模板
			downloadSelfFundInfoExcel(){
				downloadSelfFundInfoExcelApi()
			},
			// 下载自管基金列表excel
			downloadSelfFundListExcel(){
				downloadSelfFundListExcelApi({
					
				}).then(res => {
					if(res.data.msgNo == 1) {
						window.location.href = res.data.payload.file_path;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			//导入Excel文件
			uploadSelfFundInfoExcel() {
				let inputDOM = this.$refs.inputer;
				// 通过DOM取文件数据
				let file = inputDOM.files[0];
				if(file == undefined) {
					return false;
				}
				if(!/\.xl(s[xmb]|t[xm]|am|s)$/.test(file.name)) {
					dialogConfirm('info', '请选择Excel格式文件')
					inputDOM.value = '';
					return false
				}
				uploadSelfFundInfoExcelApi({}, file).then(res => {
					if(res.data.msgNo == 1) {
						dialogConfirm('success', '导入成功');
						this.currentPage = 1;
						this.getSelfFundList(1);
					} else {
						dialogConfirm('info', res.data.message);
					}
					inputDOM.value = '';
				})
			},
			
			
			// 基金运行总览
			toFundRunListPage(){
				this.$router.push({
					name: "selfAdministerFunRunList",
					query:{
						type:2,
					}
				})
			},
			// 分页功能模块
			changeCurrentPage(e){
				this.currentPage = e;
				this.getSelfFundList()
			},
			everyPageSizeChange(e){
				this.pageSize = e;
				this.getSelfFundList()
			},
		},
	}
</script>

<style scoped="scoped">
	.body {
		box-sizing: border-box;
		padding: 20px 30px 20px 30px;
		font-size: 14px;
	}
	.search_group_bottom{
		margin-top: 20px;
	}
	.search_group_bottom .search_box{
		width: 140px;
	}
	
	.search_box{
		width: 170px;
		height: 30px;
		margin-right: 13px;
	}
	.date_box{
		width: 162px;
	}
	.date_middle_text{
		margin: 0 10px;
	}
	.btn_box{
		margin-left: 24px;
	}
	.btn_box button{
		width: 80px;
		height: 30px;
		color: white;
		font-size: 14px;
		border-radius: 20px;
		background: #6292F4;
	}	
	.under_fund_header {
		margin: 26px 0;
		color: #6292F4;
		font-size: 12px;
	}
	.under_fund_header>div{
		color: #6292F4;
	}
	.under_fund_header span {
		color: #6292F4;
		padding: 0 4px;
	}
	.under_fund_header button{
		/*width: 160px;*/
		height: 30px;
		margin-left: 10px;
		padding: 0 15px;
		color: #6292F4;
		font-size: 14px;
		border-radius: 20px;
		border: 1px solid #6292F4;
		/*background: #6292F4;*/
	}
	.under_fund_header button span{
		color: #6292F4;
		font-size: 14px;
	}
	.under_fund_header_btn{
		color: white !important;
		background: #6292F4 !important;
	}
	.under_fund_header_btn span{
		color: white !important;
	}
	.dialog_btn {
		width: 94px;
		height: 36px;
		color: #6292F4;
		border-radius: 20px;
		border: 1px solid #6292F4;
	}
	
	.dialog_btn_submit {
		margin-left: 20px;
		color: white !important;
		background: #6292F4;
	}
	
	.line_box {
		margin: 30px 0;
	}
	
	.line {
		height: 1px;
		background-color: #E5E5EA;
	}
</style>


